import { Component, ChangeDetectionStrategy, ChangeDetectorRef, Input } from '@angular/core';
import { Routes, ActivatedRoute } from '@angular/router';
import { AppContainer } from 'app/gx/base/app-container';
import { CompositeNavigation } from 'app/gx/navigation/composite-navigation';
import { PanelComponent } from 'app/gx/base/panel.component';
import { BcPanelComponent} from 'app/gx/base/bc-panel.component';
import { GAMService} from "app/gx/auth/gam.service";
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { AppBarService } from "app/gx/base/app-bar.service";
import { NavigationStyle } from "app/gx/base/view-manager";
import { Settings } from "app/app.settings";

import { GetEmoji_Level_DetailService , GetEmoji_Level_DetailData } from './getemoji_level_detail.service';
import { msg as GeneXus__SD__Interop_msg } from '@genexus/web-standard-functions/dist/lib-esm/misc/msg';
import { EmojiJSONData } from 'app/EmojiJSON/emojijson.dt';
import { UITextblockElement } from 'app/gx/ui/model/ui-textblock';
import { GxSdtData } from 'app/gx/base/gxsdt.dt';
import { EmojiJSON_EmojiJSONItemData } from 'app/EmojiJSON/emojijson.dt';
import { UIFormElement } from 'app/gx/ui/model/ui-form';
import { UIActionBarElement } from 'app/gx/ui/model/ui-actionbar';

@Component({
  selector: 'GetEmoji_Level_Detail',
  templateUrl: './getemoji_level_detail.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    GetEmoji_Level_DetailService,
  ],
  styles: [":host { display: flex; flex: 1; }"]
})
export class GetEmoji_Level_DetailComponent extends PanelComponent {

  G1: GetEmoji_Level_DetailData;
  E2: EmojiJSONData;
  uiModel: GetEmoji_Level_DetailUIModel;
  uiActions: GetEmoji_Level_DetailUIActions;
  @Input('mode') 
  Mode: string;


  stateMembers = [
    ['Mode',],
    ['G1','E2','uiModel',] 
  ];

  _routingPath = 'GetEmoji-Level_Detail';
  views = [
    {
      name: "ViewAny",
      type: "any",
      os: "All",
      minShortestBound: 0,
      maxShortestBound: 0,
      minLongestBound: 0,
      maxLongestBound: 0,
      navigationStyle: "default",
      appBarInitFn: this.initAppBar_ViewAny.bind(this),
      appBarBindFn: this.bindAppBar_ViewAny.bind(this),
      appBarResetFn: this.resetAppBar_ViewAny.bind(this),
      UIModelDefaults: this.getUIModelDefaults_ViewAny.bind(this)

    }

  ];



  constructor(
      private panelService: GetEmoji_Level_DetailService,
      protected gam: GAMService,
      public app:AppContainer,
      protected nvg:CompositeNavigation,
      protected activatedRoute: ActivatedRoute,
      private appBarService: AppBarService,
      protected cdr: ChangeDetectorRef
  ) {
    super( app, nvg, activatedRoute, cdr);
    this.uiActions = new GetEmoji_Level_DetailUIActions(this);

    this.canControlAppBar = activatedRoute.component === GetEmoji_Level_DetailComponent;
    this.showAsCard = !this.canControlAppBar;
    this.initState(null);
  }

  initState(params) {
    this.G1 = new GetEmoji_Level_DetailData();
    this.E2 = new EmojiJSONData();
    this.uiModel = new GetEmoji_Level_DetailUIModel(this);
    this.Mode = !params ? this.Mode : "";

    this.loadParams(params);

    this.viewManager.update(this.Mode);
    this.updateUIModel( this.uiModel, this.viewManager.getUIModelDefaults());

    this.panelService.start();
  }

  bindAppBar_ViewAny(navigationStyle: NavigationStyle) {
    this.uiModel.ctrlApplicationbar.navigationStyle = navigationStyle;
    this.uiModel.ctrlApplicationbar.showBackButton = this.nvg.canGoBack()
    this.uiModel.ctrlApplicationbar.actionItems = [];
    this.uiModel.ctrlApplicationbar.onBackButtonClick = () => this.callAction(this.__Cancel);
    this.appBarService.bind( this.showAsCard, this.uiModel.ctrlApplicationbar);
  }

  initAppBar_ViewAny() {
    this.updateUIModel( this.uiModel, this.viewManager.getUIModelDefaults());
    this.cdr.markForCheck();
  }

  resetAppBar_ViewAny() {
  }
   

  getUIModelDefaults_ViewAny( containerName?: string) {
    if (!containerName) {
      return [
        ['ctrlApplicationbar', 'class', 'ApplicationBars'],
        ['ctrlApplicationbar', 'visible', true],
        ['ctrlApplicationbar', 'caption', this.app.translate('Get Emoji')],
        ['ctrlApplicationbar', 'showBackButton', 'False'],
        ['ctrlT_emoji', 'caption', this.app.translate('Text Block')]
      ];
    }

    return [];
  }


  // Actions
  _GET = async (): Promise<any> => {
    const __aSt = this.startAction();
    try {
      const data = await this.panelService.GetById(this.G1.Emojiid);
      this.E2 = data.EmojiJSON;

      if((this.E2.Count == 0)) {
        await GeneXus__SD__Interop_msg( this.app.translate('NO EXITE EL EMOJI'));
      }
      else {
        this.uiModel.ctrlT_emoji.caption = GxSdtData.item(this.E2, 1).emojihtml;
      }

    } catch (e) {
      this.processCompositeError(e);
    } finally {
      this.cdr.markForCheck();
      this.endAction(__aSt);
    }
  } 



  // Load and refresh
  loadParams(params) {
    if (params) {
      this.Mode = this.nvg.getParam('mode', params, 1) || "";
    }
  }

  async loadPanel() {
    this.G1 = await this.panelService.getGetEmoji_Level_Detail( );
    this.updateGxdynprops(this.G1, this.uiModel);


  }

  async Refresh(type?: string) {
    this.G1 = await this.panelService.getGetEmoji_Level_Detail( );
    this.updateGxdynprops(this.G1, this.uiModel);


    this.cdr.markForCheck();
  }



}

class GetEmoji_Level_DetailUIModel {

  private _host: GetEmoji_Level_DetailComponent;

  constructor( host: GetEmoji_Level_DetailComponent) {
    this._host = host;
    this.ctrlForm.applicationBar = this.ctrlApplicationbar;

  }

  ctrlForm = new UIFormElement();
  ctrlApplicationbar = new UIActionBarElement();
  ctrlT_emoji = new UITextblockElement();

}

   

class GetEmoji_Level_DetailUIActions {

  private _host: GetEmoji_Level_DetailComponent;

  constructor( host: GetEmoji_Level_DetailComponent) {
    this._host = host;
  }

}
 