import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Router } from "@angular/router";

import { Settings } from 'app/app.settings';
import { AppContainer } from 'app/gx/base/app-container';
import { PanelService } from "app/gx/base/panel.service";
import { EndpointConnector } from "app/gx/base/endpoint.connector";
import { GAMService } from "app/gx/auth/gam.service";
import { Type, Transform } from 'class-transformer';
import { Observable, throwError } from "rxjs";
import { catchError } from 'rxjs/operators';


import { msg as GeneXus__SD__Interop_msg } from '@genexus/web-standard-functions/dist/lib-esm/misc/msg';
import { EmojiJSONData } from 'app/EmojiJSON/emojijson.dt';
import { UITextblockElement } from 'app/gx/ui/model/ui-textblock';
import { GxSdtData } from 'app/gx/base/gxsdt.dt';
import { EmojiJSON_EmojiJSONItemData } from 'app/EmojiJSON/emojijson.dt';
import { UIFormElement } from 'app/gx/ui/model/ui-form';
import { UIActionBarElement } from 'app/gx/ui/model/ui-actionbar';

@Injectable()
export class GetEmoji_Level_DetailService extends PanelService {

    constructor(private http: HttpClient, private _router:Router, private _loginService: GAMService, private _app: AppContainer) {
      super(_router, _loginService, _app);
    }

  // Data services
  async getGetEmoji_Level_Detail( ): Promise<GetEmoji_Level_DetailData> {
        try {
          return await EndpointConnector.getDataForType<GetEmoji_Level_DetailData>(this.http, Settings.SERVICE_API_ENDPOINT + "GetEmoji_Level_Detail"+ "?gxid="+this.getGxid(0), GetEmoji_Level_DetailData);
        }
        catch (error) {
          return this.handleError(error);
        }

  }


  // Other services
  GetById = async (EmojiId?:any) : Promise<any> => {
      const body = {
        EmojiId: EmojiId
      };
      try {
        const response = await EndpointConnector.postData(this.http, Settings.SERVICE_API_ENDPOINT + "GetById", body , { 'EmojiJSON':{isCollection:false, type:EmojiJSONData} });
        return response;
      }
      catch(error)
      {
        return this.handleError(error);
      }
  };



}

// Data structures
export class GetEmoji_Level_DetailData {
  Emojiid: string;
  Gxdynprop: string;


  constructor() {
    this.Emojiid = "";
    this.Gxdynprop = "";

  }
}



 